      .alert-radio-group {
        .alert-radio-button[aria-checked="true"] {
          .button-inner {
            .alert-radio-icon {
              border-color: var(--accent_color) !important;
  
              .alert-radio-inner {
                background-color: var(--accent_color) !important;
              }
            }
  
            .alert-radio-label {
              color: var(--accent_color) !important;
            }
          }
        }
      }
  
      .alert-button-group {
        .alert-button {
          color: var(--accent_color) !important;
        }
      }
  
      .alert-input-group {
        .alert-input-wrapper {
          .alert-input:focus {
            border-bottom: 2px solid #dedede;
          }
        }
      }
    }
  
    .alert-top {
      padding: 0 !important;
      align-items: center !important;
    }
  }
  
  ion-modal.mini-cart,
  ion-modal.sort,
  ion-modal.filter,
  ion-modal.shipping {
    ion-backdrop {
      visibility: visible !important;
      z-index: 0;
    }
  
    .modal-wrapper {
      bottom: 0;
      width: 100%;
      height: auto;
      position: absolute;
  
      .header:after {
        height: 1px !important;
      }
  
      ion-header {
        background: transparent !important;
        background-color: transparent !important;
        border: none !important;
      }
      .toolbar {
        .toolbar-title {
          color: #000 !important;
        }
  
        .bar-button {
          color: #000 !important;
  
          .icon {
            color: #000 !important;
          }
        }
      }
    }



  }
  
  ion-modal.mini-cart {
    .modal-wrapper {
      min-height: 220px;
    }
  }
  
  ion-modal.sort {
    .modal-wrapper {
      min-height: 300px;
  
      .scroll-content {
        overflow: hidden;
      }
    }
  }
  
  ion-modal.filter {
    .modal-wrapper {
      min-height: 100%;
  
      .scroll-content {
        overflow: hidden;
      }
    }
  }

  ion-modal.shipping {
    .modal-wrapper {
      min-height: 280px;
  
      .scroll-content {
        overflow: hidden;
      }
    }
  }
  
  .loading-custom {
    .loading-wrapper {
      background: transparent;
      box-shadow: none;
    }
  
    .loader {
      border: 6px solid #e3e3e3;
      border-radius: 50%;
      border-top: 6px solid var(--accent_color);
      width: 48px;
      height: 48px;
      margin: 0px auto;
      animation: spin 2s linear infinite;
      -webkit-animation: spin 2s linear infinite;
    }
  
    .loader_outer {
      height: 100%;
      display: flex;
      align-items: center;
    }
  }
  
  loading-custom-msg {
    .loading-wrapper {
      background: white !important;
      box-shadow: none;
    }
  
    .loader {
      border: 6px solid #e3e3e3;
      border-radius: 50%;
      border-top: 6px solid var(--accent_color);
      width: 48px;
      height: 48px;
      margin: 0px auto;
      animation: spin 2s linear infinite;

      -webkit-animation: spin 2s linear infinite;
    }
  
    .loader_outer {
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
  
  .custom_alert {
    .alert-wrapper {
      position: absolute;
      bottom: 0;
      width: 100%;
      max-width: 100%;
      left: 0;
  
      .alert-message {
        color: #000;
        padding: 8px;
        border: 2px dashed #eee;
        margin: 0 10px 10px;
      }
  
      .alert-head {
        padding-top: 10px;
        padding-bottom: 10px;
  
        .alert-title {
          font-size: 20px;
        }
      }
  
      .alert-button-group {
        justify-content: space-between;
        padding: 8px;
        border-top: 1px solid #ccc;
  
        .alert-button {
          flex: 1;
          color: #fff !important;
          //@include margin(null, 0px, null, null);
  
